  border-bottom: 2px solid rgb(157, 193, 214);
  margin-bottom: 2px;  
  margin-left: auto;
  width: 90%;
}

.header-border-one {
  width: 100%;
}

.header-border-two {
  width: 95%;
}

.header-border-three {
  width: 90%;
}

#standings {
