  background-color: rgb(65, 108, 250);
  margin-bottom: 5px;
  padding: 3px 15px;
  font-weight: bold;
  color: white;
}

.preview-content {
  display: -ms-flexbox;
  display: flex;
}

.preview-image {
  /* flex: 1; */
}

