}

.custom-header {
  font-weight: bold;
  border-top: 1px solid lightgray;    
  border-bottom: 2px solid dimgray;
}

.custom-column {
  padding: 5px 20px 5px 10px;
  -ms-flex: 1 1;
      flex: 1 1;
